body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}


.nav-bar{text-align:center}.nav-bar .unicorn-banner{height:400px}.nav-bar .race-tile-container{display:grid;grid-template-columns:1fr 1fr 1fr;margin:0 340px}

.race-tile{border:1px solid black;border-radius:10px;margin:10px;padding:20px 0;font-size:25px;font-weight:bold}.race-tile:hover{background-color:lightgray;cursor:pointer}

